* {
  margin: 0;
}

.blogs-body {
  width: 100%;
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f1f1f1;
}

.blogs-section-one {
  width: 100%;
  height: 350px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-sizing: border-box;
  padding: 0 5%;
}

.blogs-section-one p {
  text-transform: uppercase;
  margin-bottom: 15px;
  font-size: 1.5em;
  color: rgb(255, 255, 255);
}

.blogs-section-one h2 {
  text-transform: uppercase;
  font-size: 3em;
  text-align: left;
  background: linear-gradient(to right, #ffffff 30%, #000000 90%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.blogs-section-one h2 span {
  display: block;
}

.blogs-section-two {
  color: black;
  width: 85%;
  min-height: 400px;
  margin: 20px auto;
  padding: 30px 0;
  display: flex;
  flex-wrap: wrap;
  gap: 60px;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.blogs-display {
  color: black;
  width: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, 350px);
  justify-content: space-between;
  box-sizing: border-box;
  grid-gap: 20px;
  box-sizing: border-box;
}

.bible-verse {
  width: 50%;
  font-size: 1.5em;
  font-weight: 700;
  border-bottom: 1px solid red;
  padding-bottom: 20px;
}

.bible-verse span {
  display: block;
  color: rgb(0, 0, 165);
  font-weight: 400;
}
