* {
  margin: 0;
}

.landingpage-body {
  width: 100%;
}

.landing-section-one {
  width: 100%;
  height: 350px;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-sizing: border-box;
  padding: 0 5%;
}

.landing-section-one p {
  text-transform: uppercase;
  margin-bottom: 40px;
  font-size: 1em;
}

.landing-section-one h2 {
  text-transform: uppercase;
  font-size: 2em;
  text-align: left;
  margin-bottom: 40px;
}

.landing-section-one h2 span {
  display: block;
}

.landing-section-two {
  width: 90%;
  min-height: 500px;
  margin: 10px auto;
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.landing-section-two h2 {
  text-transform: uppercase;
  font-size: 1.5em;
  text-align: left;
  margin-bottom: 10px;
}

.landing-sec-two-links {
  width: 100%;
  gap: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  flex-grow: 1;
}

.landing-section-three {
  width: 100%;
  height: 800px;
  margin: 10px auto;
  padding: 10px 5%;
  background-color: rgb(245, 245, 245);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.landing-section-three h2 {
  text-transform: uppercase;
  font-size: 1.5em;
  text-align: left;
  margin-bottom: 20px;
}

.land-sect-3-span {
  display: block;
  font-size: 1.5em;
  font-weight: 800;
}

.land-sect-three-grid {
  width: 100%;
  min-height: 500px;
  margin: 30px 0;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.grid {
  width: 90%;
}

.grid-left {
  display: none;
  height: 70%;
  margin-top: 50px;
  border-radius: 15px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.grid-center {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.grid-center-image {
  width: 100%;
  height: 90%;
  border-radius: 15px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.grid-right {
  display: none;
  height: 70%;
  margin-top: 50px;
  border-radius: 15px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.landing-section-four {
  width: 90%;
  min-height: fit-content;
  margin: 10px auto;
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.landing-section-four h2 {
  font-size: 2em;
}

.sect-four-components {
  width: 90%;
  min-height: fit-content;
  margin-top: 30px;
  gap: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

